//  
export default function modal() {

    let panel = document.querySelector('body');
    let modal = document.querySelector('.modal-wrapper'); // .modal-wrapper          
    let modals = document.querySelectorAll('.modal-wrapper > .modal');

    let elements = document.querySelectorAll('.btn-1');

    for (let elem of elements) {

        elem.addEventListener('click', e => { // .btn-1        


            for (let modal of modals) {
                modal.classList.remove("active-modal");
            }
            document.querySelector('.modal-btn-1').classList.add("active-modal");

            panel.classList.toggle('no-scroll');
            modal.classList.toggle('modal-wrapper--open'); //    .modal-wrapper--open       

        });
    }


    let elements2 = document.querySelectorAll('.btn-2');

    for (let elem2 of elements2) {

        elem2.addEventListener('click', e => { // .btn-1        


            for (let modal of modals) {
                modal.classList.remove("active-modal");
            }
            document.querySelector('.modal-btn-2').classList.add("active-modal");

            panel.classList.toggle('no-scroll');
            modal.classList.toggle('modal-wrapper--open'); //    .modal-wrapper--open       

        });
    }

    let closes = document.querySelectorAll('.close');
    for (let close of closes) {

        close.addEventListener('click', e => { //    spane   .close     
            panel.classList.remove('no-scroll');
            modal.classList.remove('modal-wrapper--open');
        });

    }

    let closes2 = document.querySelectorAll('.modal-wrapper--open');
    for (let close2 of closes2) {

        close2.addEventListener('click', e => { //    spane   .close     
            panel.classList.remove('no-scroll');
            modal.classList.remove('modal-wrapper--open');
        });

    }

    window.addEventListener('keydown', (e) => {
        if (e.code === 'Escape') {
            let panel = document.querySelector('body');
            let modal = document.querySelector('.modal-wrapper');
            panel.classList.remove('no-scroll');
            modal.classList.remove('modal-wrapper--open');
        }
    });

    document.addEventListener('click', (e) => { //     
        if (e.target === modal) { //    - , :

            panel.classList.remove('no-scroll');
            modal.classList.remove('modal-wrapper--open');
        }
    });



}